.App {
  margin-top: 5%;
  margin-left: 5%;
  margin-right: 5%;
}

h1{
  font-size: 50pt;
}

.Details{
  font-weight: 100;
  font-size: 15pt;
}

.Content{
  display: flex;
  justify-content: space-around;
}

a{
  margin-right: 20pt;
}

.Picture{
  margin-left: 30pt;
  height: 50vmin;
  width: 50vmin;
  object-fit: cover;
  border-radius: 50%;
}

.Links{
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}